.background {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100dvw;
  height: 100dvh;
  overflow: hidden;
  z-index: -1;
  background-size: cover;
  background-position: center;

  svg {
    position: absolute;
    width: 0;
    height: 0;
  }

  .blob-container {
    width: 100dvw;
    height: 100dvh;
    filter: url(#goo);
  }

  .blob {
    position: absolute;
    border-radius: 9999px;
    width: 100%;
    height: 100%;
    animation: move 20s infinite ease-in-out alternate;
    transition: scale(4s ease-in-out);
    mix-blend-mode: screen;
    filter: blur(9999px);

    &.blob1 {
      background: var(--color1);
      animation-delay: 0s;
    }

    &.blob2 {
      background: var(--color2);
      animation-delay: 4s;
    }

    &.blob3 {
      background: var(--color3);
      animation-delay: 8s;
    }

    &.blob4 {
      background: var(--color4);
      animation-delay: 12s;
    }

    &.blob5 {
      background: var(--color5);
      animation-delay: 16s;
    }
  }
}

@keyframes move {
  0% {
    transform: translate(-10%, -10%) scale(1);
  }
  25% {
    transform: translate(30%, -40%) scale(1.2);
  }
  50% {
    transform: translate(70%, 20%) scale(1.5);
  }
  75% {
    transform: translate(-30%, 40%) scale(1.1);
  }
  100% {
    transform: translate(-10%, -10%) scale(1);
  }
}

@media (prefers-color-scheme: dark) {
  .background {
    background-image: url('../../assets/page/Background_Dark.png');
  }
  :root {
    --color1: rgba(25, 0, 47, 0.8);
    --color2: rgba(40, 0, 48, 0.8);
    --color3: rgba(10, 0, 52, 0.8);
    --color4: rgba(0, 45, 8, 0.8);
    --color5: rgba(0, 25, 47, 0.8);
  }
}

@media (prefers-color-scheme: light) {
  .background {
    background-image: url('../../assets/page/Background_Light.png');
  }
  :root {
    --color1: rgba(108, 32, 175, 0.8);
    --color2: rgba(146, 30, 170, 0.8);
    --color3: rgba(62, 34, 173, 0.8);
    --color4: rgba(50, 146, 68, 0.8);
    --color5: rgba(46, 101, 149, 0.8);
  }
}